import React from "react";
import { Paper, Typography, TextField, InputAdornment, IconButton, CircularProgress, Divider, Button, FormLabel } from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import API from "../Services/API";
import { Link } from "react-router-dom";

export default class SignIn extends React.Component {
    state = {
        nombre: "",
        identificacion: "",
        telefono: "",
        email: "",
        fechaNacimiento: "",
        pais: "",
        ciudad: "",
        direccion: "",
        username: "",
        password: "",
        confirm_password: "",
        error: null,
        loading: false,
        fin_registro: null
    }
    handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value });
    }
    handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        event.stopPropagation();
        if (this.state.loading) return;
        var error = Validar(this.state);
        if (error) return this.setState({ error });
        this.setState({ loading: true, error: null });
        setTimeout(() => {
            API.SignIn(this.state)
                .then(x => this.setState({ loading: false, fin_registro: x.email })
                ).catch(error => this.setState({ error, loading: false }));
        }, 2000);
    }
    render() {
        const { state } = this;
        const { loading, error, fin_registro } = this.state;

        if (fin_registro) return (<div style={{ width: "100%", padding: 30, textAlign: "center" }}>
            <Typography variant="h4">Registro completado!</Typography>
            <Typography variant="subtitle1">
                Para habilitar tu cuenta entra a tu correo ({fin_registro})
            y abre el enlace que te enviamos.
            </Typography>
        </div>);

        return (<main style={{ width: "100%", paddingTop: 10, textAlign: "center", backgroundColor: "#fafafa" }}>
            <Paper style={{ padding: 20, margin: "auto", maxWidth: 600 }} elevation={10}>
                <form noValidate onSubmit={this.handleSubmit}>
                    <div style={{ display: "flex", flexDirection: "row" }}>
                        <img style={{ height: 100 }} src="http://natucosmetic.com/wp-content/uploads/2020/03/LOGO-NATU-PNG.png"></img>
                        <div style={{ flexGrow: 1, margin: 20, textAlign: "left" }}>
                            <Typography variant="h4">REGISTRATE</Typography>
                            <Typography component={Link} to="/login" >Ya tienes una cuenta? Inicia sesión aquí</Typography>
                        </div>
                    </div>
                    <Divider />
                    <Typography variant="h6" style={{ marginTop: 20 }}>Información personal</Typography>
                    <Input disabled={loading} error={error && error.nombre} onChange={this.handleChange} value={state.nombre} name="nombre" label="Nombres Completos" />
                    <Input disabled={loading} error={error && error.identificacion} onChange={this.handleChange} value={state.identificacion} name="identificacion" label="Identificacion (Cedula, pasaporte, etc.)" type="phone" />
                    <Input disabled={loading} error={error && error.telefono} onChange={this.handleChange} value={state.telefono} name="telefono" label="Telefono" type="phone" />
                    <Input disabled={loading} error={error && error.email} onChange={this.handleChange} value={state.email} name="email" label="Correo Electronico" type="email" />
                    <Input disabled={loading} error={error && error.fechaNacimiento} onChange={this.handleChange} value={state.fechaNacimiento} name="fechaNacimiento" label="Fecha de Nacimiento" type="date" />

                    <Typography variant="h6" style={{ marginTop: 30 }}>Agrega tu dirección</Typography>
                    <Input disabled={loading} error={error && error.pais} onChange={this.handleChange} value={state.pais} name="pais" label="Pais" />
                    <Input disabled={loading} error={error && error.ciudad} onChange={this.handleChange} value={state.ciudad} name="ciudad" label="Ciudad" />
                    <Input disabled={loading} error={error && error.direccion} onChange={this.handleChange} value={state.direccion} name="direccion" label="Direccion" />

                    <Typography variant="h6" style={{ marginTop: 30 }}>Crea tu Usuario</Typography>

                    <Input disabled={loading} error={error && error.username} onChange={this.handleChange} value={state.username} name="username" label="Usuario" />
                    <Password disabled={loading} error={error && error.password} onChange={this.handleChange} value={state.password} name="password" label="Contraseña" />
                    <Password disabled={loading} error={error && error.confirm_password} onChange={this.handleChange} value={state.confirm_password} name="confirm_password" label="Confirma tu contraseña" />

                    {(error && error.guardar) ? <Typography color="secondary">{error.guardar}</Typography> : null}
                    <Button type="submit" color="primary" variant="outlined" fullWidth style={{ marginTop: 20 }} >
                        {loading ? <CircularProgress size={20} color="inherit" /> : "ENVIAR"}
                    </Button>
                </form>
            </Paper>
        </main>);
    }
}
function Validar(state: any) {
    const { nombre,
        telefono,
        identificacion,
        email,
        fechaNacimiento,
        pais,
        ciudad,
        direccion,
        username,
        password,
        confirm_password
    } = state;
    const result: any = {};
    if (isEmpty(nombre)) result.nombre = "Digita tu nombre";

    if (isEmpty(telefono)) {
        result.telefono = "Digita tu telefono";
    } else {
        if (isNaN(parseInt(telefono))) result.telefono = "El telefono solo puede contener numeros";
        if (telefono.length <= 5) result.telefono = "El telefono debe tener mínimo 6 digitos";
    }

    if (isEmpty(identificacion)) {
        result.identificacion = "Digita tu identificacion";
    }

    if (isEmpty(email)) {
        result.email = "Digita tu email";
    } else {
        if (email.indexOf("@") === -1) result.email = "El correo es incorrecto";
        if (email.indexOf(".") === -1) result.email = "El correo es incorrecto";
    }

    if (isEmpty(fechaNacimiento)) {
        result.fechaNacimiento = "Digita tu fecha de nacimiento";
    } else {
        if (isNaN(+new Date(fechaNacimiento))) result.fechaNacimiento = "La fecha no es válida";

    }

    if (isEmpty(pais)) result.pais = "Digita tu pais";
    if (isEmpty(ciudad)) result.ciudad = "Digita tu ciudad";
    if (isEmpty(direccion)) result.direccion = "Digita tu direccion";

    if (isEmpty(username)) result.username = "Digita tu Usuario";
    if (isEmpty(password)) {
        result.password = "Digita la contraseña";
    } else {
        if (password.length < 5) result.password = "La contraseña debe tener más de 6 caracteres";
        if (password.trim() !== confirm_password.trim()) result.confirm_password = "Las contraseñas no concuerdan entre si";
    }
    if (Object.keys(result).length) {
        var key = Object.keys(result)[0];
        window.requestAnimationFrame(() => document.getElementById(key).focus());
        return result;
    }
    return null;
}
function isEmpty(value) {
    return !(value && value.length);
}
function Input(props: { name: string, value: any, onChange: any, label: string, type?: string, disabled?: boolean, error?: any }) {
    const { error, type, label, ...other_props } = props;
    var input_props: any = { type, inputProps: { id: props.name } };
    if (error) {
        input_props.error = true;
        input_props.helperText = error;
    }
    if (type === "date")
        return (<div style={{ width: "100%", display: "flex", flexDirection: "row", marginBottom: 5, marginTop: 15 }}>
            <FormLabel style={{ paddingTop: 8, marginRight: 5 }}>{label}</FormLabel>
            <TextField
                required

                {...input_props}
                style={{ flex: 1 }}
                {...other_props}
            />
        </div>);

    input_props.label = label;

    return (<TextField
        fullWidth
        required
        {...input_props}
        style={{ marginTop: 5, marginBottom: 5 }}
        {...other_props}
    />);
}
function Password(props: any) {
    const { error, ...other_props } = props;
    const [show_password, setPassword] = React.useState(false);
    var input_props: any = { inputProps: { id: props.name } };
    if (error) {
        input_props.error = true;
        input_props.helperText = error;
    }
    return (<TextField
        type={show_password ? "text" : "password"}
        {...other_props}
        {...input_props}
        fullWidth
        required
        style={{ marginTop: 5, marginBottom: 5 }}
        InputProps={{
            endAdornment: <InputAdornment position="end" >
                <IconButton onClick={x => setPassword(!show_password)}>
                    {show_password ? <Visibility /> : <VisibilityOff />}
                </IconButton>
            </InputAdornment>
        }}
    />);
}